import React from 'react';
import { SeccionLateral2 } from './SeccionLateral2';
import img_grupo_medicos from '../Assents/img/img-grupo-medicos.jpg';


export const QuieneSomos = () => {
  return (
    <div >
        <div className="contenedor-QuienesSomos">
            <div className="contenedor-sections">
                <div className="section1-routs">
                    <div class="container col-xxl-8 px-4 py-5">
                        <div class="row flex-lg-row-reverse align-items-center g-5 py-5">
                        <div class="col-10 col-sm-8 col-lg-6">
                            <img src={img_grupo_medicos} class="d-block mx-lg-auto img-fluid" alt="Bootstrap Themes" width="700" height="500" loading="lazy"/>
                        </div>
                        <div class="col-lg-6" id="text-quienes-somos">
                            <h1 class="display-5 fw-bold text-body-emphasis lh-1 mb-3">Historia</h1>
                            <p class="lead">El centro fue fundado el 10 de marzo de 1989 por un grupo de médicos emprendedores de gran prestigio en sus respectivas especialidades, cuyo objetivo fue desarrollar un ambicioso proyecto centrado en construir una clínica de excelencia, con una fuerte vocación de vanguardia e innovación y con una preocupación constante por la calidad y la eficiencia en el cuidado médico, motivo por el cual desde su fundación , recibió varias distinciones a la excelencia tanto nacionales como internacionales.</p>
                            
                            <h2>Msión</h2>
                            <p class="lead">"Ser lideres, modelo y ejemplo a seguir en la prestación de servicios de salud. Prestar servicios de calidad con compromiso"</p>

                            <h2>Visíon</h2>
                            <p class="lead">"Transformar el cuidado de la salud en esperanza para las familias, con profesionales altamente calificados"</p>
                        </div>
                        </div>
                    </div>
                </div>

                <div className="section2">
                    <SeccionLateral2/>
                </div>
            </div>
        </div>
    </div>
  )
}
