import React from 'react';
import Image from 'react-bootstrap/Image';
import Card from 'react-bootstrap/Card';
import img_emergencias from '../Assents/img/img-emergencia.jpg';
import { SeccionLateral } from './SeccionLateral'
import img_doc2 from '../Assents/img/img-doc-2.jpg'
import img_doc5 from '../Assents/img/img-doc-5.jpg'



export const Emergencia = () => {
  return (
    <div >
        <div className="contenedor-Emergencias">
            <div className="contenedor-sections">
                <div className="section1-routs" id="section-routs-emer">
                    <h1>Emergencias</h1>
                    <Image src={img_emergencias} fluid className="img-emergencias"/>;
                    <h3>Bienvenido a Nuestra Sala de Emergencias</h3>
                    <p>
                    En nuestra institución médica, la sala de emergencias es el corazón palpitante de atención inmediata y cuidado compasivo. Nos dedicamos a proporcionar asistencia médica urgente las 24 horas del día, los 7 días de la semana, para atender a pacientes con situaciones que requieren atención inmediata.
                    </p>

                    <h3>Atención Inmediata y Calidad Médica</h3>
                    <p>
                    Nuestro equipo altamente capacitado de médicos, enfermeras y personal de apoyo está siempre listo para recibir a pacientes en sus momentos más críticos. Equipados con tecnología de vanguardia y recursos médicos avanzados, abordamos una amplia gama de emergencias médicas, desde lesiones traumáticas hasta enfermedades súbitas.
                    </p>


                    <h3>Contáctenos</h3>
                    <p>
                    Si necesita asistencia médica de emergencia, no dude en visitar nuestra sala de emergencias o llamar al número de emergencias correspondiente. Estamos comprometidos a ser su recurso confiable en momentos críticos.

En nuestra sala de emergencias, su salud y bienestar son nuestra principal preocupación. Confíe en nosotros para brindarle la atención que necesita cuando más lo necesita.
                    </p>
                    <h2>Medicos de Guardia</h2>

                    <div className="cards-medicos-guardia">
                        <Card style={{ width: '18rem' }}>
                            <Card.Img variant="top" src={img_doc2} />
                            <Card.Body>
                                <Card.Title>Dr. Javier Rodríguez</Card.Title>
                            </Card.Body>
                        </Card>
                        <Card style={{ width: '18rem' }}>
                            <Card.Img variant="top" src={img_doc5} />
                            <Card.Body>
                                <Card.Title>Dra. Laura Fernández</Card.Title>
                            </Card.Body>
                        </Card>
                    </div>
                    

                </div>

                <div className="section2">
                    <SeccionLateral/>
                </div>
            </div>
        </div>
    </div>
  )
}
