import React from 'react';
import Carousel from 'react-bootstrap/Carousel'
import img_banner1 from '../Assents/img/img_banner1.jpg'
import img_banner2 from '../Assents/img/img_banner2.jpg'
import img_banner3 from '../Assents/img/img_banner3.jpg'
import img_banner4 from '../Assents/img/img_banner4.jpg'
import img_banner5 from '../Assents/img/img_banner5.jpg'
import img_banner6 from '../Assents/img/img_banner6.jpg'




export const Banner2 = () => {
  return (

    <div className="contenedor-carrusel" >
        <div className="titulo-banner">
            <span >
                <h1>
                    Centro de Salud San Andrés
                </h1>
            </span>
        </div>

        <Carousel fade className="carrusel1">
            <Carousel.Item>
                <div><img src={img_banner1} alt="..."/></div>
                <Carousel.Caption className="text-banner1">
                    <h3>Cuidando tu salud, brindando esperanza.</h3>
                </Carousel.Caption>
            </Carousel.Item>

            <Carousel.Item>
                <div><img src={img_banner2} alt="..."/></div>
                <Carousel.Caption className="text-banner1">
                <h3>Cuidando de ti en cada paso del camino.</h3>
                </Carousel.Caption>
            </Carousel.Item>

            <Carousel.Item>
                <div><img src={img_banner3} alt="..."/></div>
                <Carousel.Caption className="text-banner1">
                <h3>Cada vida importa, cada cuidado cuenta.</h3>
                </Carousel.Caption>
            </Carousel.Item>
        </Carousel>


        <Carousel fade className="carrusel2">
            <Carousel.Item className="img-parrafo">
                <div className="parrafos-imgs">
                    <div className="div-img-parrafo"><img src={img_banner4} alt="..."/></div>
                    <div className="div-img-parrafo">
                        <h3>NOTICIAS</h3>
                        <p>
                        ¡Nuestro hospital celebra 20 años de compromiso con la salud comunitaria! A lo largo de estas dos décadas, hemos estado a la vanguardia de la atención médica, brindando cuidados compasivos y servicios innovadores a nuestros pacientes. Únete a nosotros mientras seguimos marcando la diferencia en la vida de las personas.
                        </p>
                    </div>
                </div>
                
            </Carousel.Item>
            <Carousel.Item className="img-parrafo">
                <div className="parrafos-imgs">
                    <div className="div-img-parrafo"><img src={img_banner5} alt="..."/></div>
                    <div className="div-img-parrafo">
                        <h3>NOTICIAS</h3>
                        <p>
                        Programa de cuidado infantil ampliado: ¡más servicios, más sonrisas! Estamos encantados de anunciar la expansión de nuestro programa pediátrico, con servicios especializados adicionales para niños y familias. En nuestro compromiso con la atención integral, ofrecemos lo mejor para los más pequeños.
                        </p>
                    </div>
                </div>
                
            </Carousel.Item>

            <Carousel.Item className="img-parrafo">
                <div className="parrafos-imgs">
                    <div className="div-img-parrafo"><img src={img_banner6} alt="..."/></div>
                    <div className="div-img-parrafo">
                        <h3>NOTICIAS</h3>
                        <p>
                        Cuidado mental y emocional: prioridades en nuestra misión. Reconociendo la importancia de la salud mental, estamos ampliando nuestros servicios para ofrecer un enfoque holístico hacia el bienestar emocional. Juntos, construyamos un futuro más saludable y equilibrado
                        </p>
                    </div>
                </div>
                
            </Carousel.Item>
        </Carousel>
    </div>
    
    
  )
}
