import React from 'react'
import Card from 'react-bootstrap/Card'
import img_doc1 from '../Assents/img/img-doc-1.jpg'
import img_doc2 from '../Assents/img/img-doc-2.jpg'
import img_doc3 from '../Assents/img/img-doc-3.jpg'
import img_doc4 from '../Assents/img/img-doc-4.jpg'
import img_doc5 from '../Assents/img/img-doc-5.jpg'
import img_doc6 from '../Assents/img/img-doc-6.jpg'


export const Doctores = () => {
  return (
    <>
        <center>
            <div className="containerDoctores">
                <div className="desc-doctores">
                    <h1>Nuestros Doctores</h1>
                    <p>Los doctores de nuestro hospital son un equipo excepcionalmente capacitado y dedicado, comprometido con brindar la más alta calidad de atención médica a nuestros pacientes. Nuestros profesionales médicos, con una vasta experiencia y especialidades diversas, están comprometidos con el bienestar de cada individuo que busca nuestros servicios. Además de su experiencia clínica, se distinguen por su empatía, atención personalizada y un enfoque integral para abordar las necesidades de salud de cada paciente. Están constantemente actualizados en las últimas tecnologías y avances médicos, asegurando así un tratamiento actualizado y de vanguardia para todos aquellos que confían en nosotros para su cuidado médico.</p>
                    <br/>
                    <p>Nuestro equipo de médicos no solo se destaca por su experiencia clínica, sino también por su compromiso con la innovación médica y la investigación continua. Estamos dedicados a adoptar las últimas tecnologías y avances científicos para mejorar los resultados de nuestros pacientes. Además, valoramos profundamente la relación médico-paciente y nos esforzamos por crear un entorno acogedor y de confianza donde los pacientes se sientan escuchados y comprendidos. En conjunto, nuestro equipo de médicos se enorgullece de proporcionar un estándar de atención médica excepcional, siempre con el objetivo de mejorar la salud y el bienestar de quienes confían en nosotros para su cuidado.</p>
                </div>

                <div className="cards-doctores">
                    <Card className="cards-doctores-c" style={{ width: '18rem' }}>
                        <Card.Img className="rounded-circle" variant="top" src={img_doc1} />
                        <Card.Body>
                            <Card.Title>Dr. Ana Gómez - Cardióloga</Card.Title>
                            <Card.Text>
                            La Dra. Ana Gómez es una cardióloga altamente experimentada, reconocida por su pasión y dedicación en el tratamiento de enfermedades cardiovasculares.
                            </Card.Text>
                        </Card.Body>
                    </Card>
                    <br/>
                    <Card className="cards-doctores-c" style={{ width: '18rem' }}>
                        <Card.Img className="rounded-circle" variant="top" src={img_doc2} />
                        <Card.Body>
                            <Card.Title>Dr. Javier Rodríguez - Oncólogo</Card.Title>
                            <Card.Text>
                            Con una sólida formación en oncología, el Dr. Javier Rodríguez se especializa en el tratamiento integral del cáncer. 
                            </Card.Text>
                        </Card.Body>
                    </Card>
                    <br/>
                    <Card className="cards-doctores-c" style={{ width: '18rem' }}>
                        <Card.Img className="rounded-circle" variant="top" src={img_doc3} />
                        <Card.Body>
                            <Card.Title>Dra. María Pérez - Neuróloga</Card.Title>
                            <Card.Text>
                            La Dra. María Pérez es una neuróloga apasionada por el estudio y tratamiento de enfermedades neurológicas.
                            </Card.Text>
                        </Card.Body>
                    </Card>
                    <br/>
                    <Card className="cards-doctores-c" style={{ width: '18rem' }}>
                        <Card.Img className="rounded-circle" variant="top" src={img_doc4} />
                        <Card.Body>
                            <Card.Title>Dr. Carlos Martínez - Cirujano Ortopédico</Card.Title>
                            <Card.Text>
                            Reconocido por su destreza quirúrgica y su enfoque conservador en el tratamiento de lesiones musculoesqueléticas, el Dr. Carlos Martínez es un cirujano ortopédico.
                            </Card.Text>
                        </Card.Body>
                    </Card>
                    <br/>
                    <Card className="cards-doctores-c" style={{ width: '18rem' }}>
                        <Card.Img className="rounded-circle" variant="top" src={img_doc5} />
                        <Card.Body>
                            <Card.Title>Dra. Laura Fernández - Pediatra</Card.Title>
                            <Card.Text>
                            La Dra. Laura Fernández es una pediatra comprometida con el bienestar infantil. Su enfoque preventivo y su capacidad para establecer vínculos de confianza con sus jóvenes pacientes y sus familias.
                            </Card.Text>
                        </Card.Body>
                    </Card>
                    <br/>
                    <Card className="cards-doctores-c" style={{ width: '18rem' }}>
                        <Card.Img className="rounded-circle" variant="top" src={img_doc6} />
                        <Card.Body>
                            <Card.Title>Dr. Alejandro Herrera - Psiquiatra</Card.Title>
                            <Card.Text>
                            El Dr. Alejandro Herrera es un psiquiatra reconocido por su comprensión empática y su enfoque integral en el tratamiento de trastornos mentales. 
                            </Card.Text>
                        </Card.Body>
                    </Card>

                </div>
            </div>
        </center>
        
    </>
  )
}
