import React from 'react'
import { SeccionLateral2 } from './SeccionLateral2';
import ReactPlayer from 'react-player';

export const Instalaciones = () => {
  return (
    <div >
        <div className="contenedor-Instalaciones">
            <div className="contenedor-sections-instalaciones">
                <div className="section1-routs-instalaciones">

                    <div className="texto-instalaciones-ref">
                        <h1>
                            Instalaciones
                        </h1>
                        <h3>Contamos con nuevas intalaciones de alta tecnologia</h3>
                    </div>
                    <div className="video-player" style={{width:'50%', height:'50%', position:'absolute'}}>
                        <ReactPlayer
                            url='https://www.youtube.com/watch?v=hXn4vXk3yO0'
                            width='100%'
                            height='100%'
                            controls
                            playing
                        />
                    </div>

                </div>

                <div className="section2">
                    <SeccionLateral2/>
                </div>
            </div>
        </div>
    </div>
  )
}
