import React from 'react'
import { SeccionLateral2 } from './SeccionLateral2';
import L from "leaflet";
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import icon from "leaflet/dist/images/marker-icon.png";
import iconShadow from "leaflet/dist/images/marker-icon.png";

let iconUbicacion = new L.icon({
    iconUrl: icon,
    iconShadow: iconShadow,
    iconSize: [50, 50],
    iconAnchor: [22, 94],
    shadowAnchor: [22, 94],
    popupAnchor: [-10, -76]
});

export const ComoLlegar = () => {
  return (
    <div >
        <div className="contenedor-ComoLlegar">
            <div className="contenedor-sections">
                <div className="section1-routs-mapa">
                    <MapContainer center={[-16.50384, -68.12946]} zoom={20} scrollWheelZoom={false} className="mapa">
                        <TileLayer
                            attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                        />
                        <Marker position={[-16.50397, -68.12950]} icon={iconUbicacion}>
                            <Popup>
                            Centro de Salud <br /> Carrera d Informatica
                            </Popup>
                        </Marker>
                    </MapContainer>

                    <div className="texto-mapa-ref">
                        <h1>
                            Encuentranos calle Federico Zuazo.
                        </h1>
                        <h3>El centro de Salud atiende 24/7</h3>
                    </div>
                </div>

                <div className="section2">
                    <SeccionLateral2/>
                </div>
            </div>
        </div>
    </div>
  )
}
