import React from 'react'
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import img_SecLateral1 from '../Assents/img/img-SecLatera1.jpg'

export const SeccionLateral = () => {
  return (
    <div className="Seccion-Lateral">
        <div className="advertencia-card-lateral">
            <Card style={{ width: '18rem' }} className="text-card-lateral">
                <Card.Img variant="top" src={img_SecLateral1} />
                <Card.Body>
                    <Card.Title>Emergencia</Card.Title>
                    <Card.Text>
                    Se reporta un aumento de los casos de Covid-19 en un 95%.
                    </Card.Text>
                </Card.Body>
                <ListGroup className="list-group-flush">
                    <ListGroup.Item>Se recomienda el uso de cubre bocas.</ListGroup.Item>
                    <ListGroup.Item>Podria haber un desbordamiento de casos.</ListGroup.Item>
                </ListGroup>
                <Card.Body>
                    <Card.Link href="/emergencias">Emergencias</Card.Link>
                    <Card.Link href="/comollegar">Como llegar</Card.Link>
                </Card.Body>
            </Card>
        </div>
        <div>
            <>
                {[
                    'Primary',
                ].map((variant) => (
                    <Card
                    bg={variant.toLowerCase()}
                    key={variant}
                    text={variant.toLowerCase() === 'light' ? 'dark' : 'white'}
                    style={{ width: '18rem' }}
                    className="mb-2"
                    >
                    <Card.Header>Especialidades</Card.Header>
                    <Card.Body>
                        <Card.Text>
                        El centro cuenta con diferentes especialidades médicas. Siga el siguiente enlace para mayor información:
                        </Card.Text>
                        <Card.Body>
                            <Card.Link href="/especialidades" className="link-cards-latera">Especialidades</Card.Link>
    
                        </Card.Body>
                    </Card.Body>
                    </Card>
                ))}
            </>
        </div>

        <div>
            <>
                {[
                    'Info',
                ].map((variant) => (
                    <Card
                    bg={variant.toLowerCase()}
                    key={variant}
                    text={variant.toLowerCase() === 'light' ? 'dark' : 'white'}
                    style={{ width: '18rem' }}
                    className="mb-2"
                    >
                    <Card.Header>Servicios</Card.Header>
                    <Card.Body>
                        <Card.Text>
                        El centro cuenta con diferentes servicios médicos. Siga el siguiente enlace para mayor información:
                        </Card.Text>
                        <Card.Body>
                            <Card.Link href="/servicios" className="link-cards-latera">Servicios</Card.Link>
    
                        </Card.Body>
                    </Card.Body>
                    </Card>
                ))}
            </>
        </div>
        

    </div>
  )
}
