import React from 'react'
import Card from 'react-bootstrap/Card';
import CardGroup from 'react-bootstrap/CardGroup';
import img_anest from '../Assents/img/img_1_anesteci.jpg';
import img_cardio from '../Assents/img/img_2_cardio.jpg';
import img_otorrino from '../Assents/img/img_3_otorrino.jpg';
import img_radiologia from '../Assents/img/img_4_radiologia.jpg';
import img_medint from '../Assents/img/img_5_medint.jpg';
import img_neurologia from '../Assents/img/img_6_neurologia.jpg';


import { SeccionLateral2 } from './SeccionLateral2';

export const Especialidades = () => {
  return (
    <div >
        <div className="contenedor-ComoLlegar">
            <div className="contenedor-sections">
                <div className="section1-routs" id="section1-routs-esp">
                    <CardGroup className="card-group-esp">
                        <Card className="card-card">
                            <Card.Img variant="top" src={img_anest} />
                            <Card.Body className="card-body-esp">
                            <Card.Title>Anestesiología</Card.Title>
                            </Card.Body>
                        </Card>
                        <Card className="card-card">
                            <Card.Img variant="top" src={img_cardio} />
                            <Card.Body className="card-body-esp">
                            <Card.Title>Cardiología</Card.Title>
                            </Card.Body>
                        </Card>
                        <Card className="card-card">
                            <Card.Img variant="top" src={img_otorrino} />
                            <Card.Body className="card-body-esp">
                            <Card.Title>Otorrinolaringología</Card.Title>
                            </Card.Body>
                        </Card>
                    </CardGroup>

                    <CardGroup className="card-group-esp">
                        <Card className="card-card">
                            <Card.Img variant="top" src={img_radiologia} />
                            <Card.Body className="card-body-esp">
                            <Card.Title>Radiología</Card.Title>
                            </Card.Body>
                        </Card>
                        <Card className="card-card">
                            <Card.Img variant="top" src={img_medint} />
                            <Card.Body className="card-body-esp">
                            <Card.Title>Medicina Interna</Card.Title>
                            </Card.Body>
                        </Card>
                        <Card className="card-card">
                            <Card.Img variant="top" src={img_neurologia} />
                            <Card.Body className="card-body-esp">
                            <Card.Title>Neurología</Card.Title>
                            </Card.Body>
                        </Card>
                    </CardGroup>
                </div>
                <div className="section2">
                    <SeccionLateral2/>
                </div>
            </div>
        </div>
    </div>
  )
}
