import React from 'react'
import { Navbar, Container, Nav, NavDropdown } from "react-bootstrap";
import { useState, useEffect } from 'react';
import logo from '../Assents/img/logo.png';
import navIcon1 from '../Assents/img/linkedin.png';
import navIcon2 from '../Assents/img/facebook.png';
import navIcon3 from '../Assents/img/youtube.png';
import { NavLink } from 'react-router-dom'



export const NavBar = () => {

    const [scrolled, setScrolled] = useState(false);
    const [activeLink, setActiveLink] = useState('home');


    useEffect(()=>{
        const onScroll = () =>{
            if(window.scrollY > 50){
                setScrolled(true);
            }else{
                setScrolled(true);
            }
        }
        window.addEventListener("scroll", onScroll);
        return () => window.removeEventListener("scroll", onScroll);
    },[])

    const onUpdateActiveLink = (value) => {
        setActiveLink(value);
    }

  return (
    <Navbar expand="lg" className={scrolled ? "scrolled":""}>
      <Container>
        <Navbar.Brand href="/">
            <img src={logo} alt="Logo" />
        </Navbar.Brand>

        <Navbar.Toggle aria-controls="basic-navbar-nav" >
            <span className="navbar-toggler-icon"></span>
        </Navbar.Toggle>
        
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <NavLink to="/home"  className="link-nav-bar">Home</NavLink>
            <NavLink to="/menu"  className="link-nav-bar">Menu</NavLink>


            <NavDropdown title="El Centro" id="basic-nav-dropdown" className="contenedor-NavDropDown">
              <NavDropdown.Item className="nav-DropD"><NavLink to="/quienessomos" >Quiénes somos</NavLink></NavDropdown.Item>
              <NavDropdown.Item className="nav-DropD"><NavLink to="/comollegar" >Cómo llegar</NavLink></NavDropdown.Item>
              <NavDropdown.Item className="nav-DropD"><NavLink to="/instalaciones" >Instalaciones</NavLink></NavDropdown.Item>
              <NavDropdown.Item className="nav-DropD"><NavLink to="/emergencias" >Emergencias</NavLink></NavDropdown.Item>
            </NavDropdown>

            <NavLink to="/doctores"  className="link-nav-bar">Doctores</NavLink>

            <NavDropdown title="Servicios" id="basic-nav-dropdown" className="contenedor-NavDropDown">
              <NavDropdown.Item href="especialidades" className="nav-DropD">Especialidades</NavDropdown.Item>
              <NavDropdown.Item href="/servicios" className="nav-DropD">Servicios</NavDropdown.Item>
              <NavDropdown.Item href="/emergencias" className="nav-DropD">Emergencias</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="/acercade" className="nav-DropD">
                Acerca De/Creditos
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
          
        <span className="navbar-text">
            <div className="social-icon">
                <a href="/"><img src={navIcon1} alt=""/></a>
                <a href="/"><img src={navIcon2} alt=""/></a>
                <a href="/comollegar"><img src={navIcon3} alt=""/></a>
            </div>
            <button className="vvd"><span><NavLink to="/login">Login</NavLink></span></button>
        </span>

        </Navbar.Collapse>
      </Container>
    </Navbar>
  )
}
